/* lexend-300 - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Lexend';
    font-style: normal;
    font-weight: 300;
    src: url('../fonts/lexend-v24-latin-300.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}
/* lexend-600 - latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: 'Lexend';
    font-style: normal;
    font-weight: 600;
    src: url('../fonts/lexend-v23-latin-600.woff2') format('woff2'); /* Chrome 36+, Opera 23+, Firefox 39+, Safari 12+, iOS 10+ */
}

:root {
    font-variant-ligatures: none;
}

html {
    font-size: var(--base-font-size);
}

body {
    font-family: var(--ff-body);
    font-weight: var(--fw-400);
    font-size: var(--fs-400);
    line-height: 1.4;
    font-style: normal;
    color: var(--col-dark);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,
.h1,
h2,
.h2,
h3,
.h3 {
    font-family: var(--ff-heading);
    font-weight: var(--fw-600);
    line-height: 1.1;
    text-wrap: balance;
    margin-bottom: 0.5em;

    color: var(--col-blue-400);
}

h1,
.h1 {
    font-size: var(--fs-900);
}
h2,
.h2 {
    font-size: var(--fs-700);
}
h3,
.h3 {
    font-size: var(--fs-600);
}
h4,
.h4 {
    font-size: var(--fs-500);
}
h5,
.h5 {
    font-size: var(--fs-500);
}

small,
.has-small-font-size,
.fs-300 {
    font-size: var(--fs-300);
}
.fs-400 {
    font-size: var(--fs-400);
}
.has-medium-font-size,
.fs-500 {
    font-size: var(--fs-500);
}
.has-large-font-size,
.fs-600 {
    font-size: var(--fs-600);
}

.fw-400 {
    font-weight: var(--fw-400);
}
strong,
.fw-bold,
.fw-600 {
    font-weight: var(--fw-600);
}

.text-balance {
    text-wrap: balance;
}

.w-constrained {
    max-width: 90ch;
}

